* {
  box-sizing: border-box;
}
/* Global */
body,
.App {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 16px;
}
button {
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  font-size: 20px;
}
.flex-container {
  display: flex;
}
/* Navbar */
nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #31a2ac;
  min-height: 65px;
  
  border-bottom: 3px solid#2f2f28;
}
.nav-left{
  margin-left: 5vw;
}
.btn{
  z-index: 1;
	background-color: #31a2ac;
	position: relative;
	display: inline-block;
	font-size: 20px;
  min-width: 80px;;
	padding: 3px 10px;
	color: #f0effe;
	text-decoration: none;
	border: solid 5px #2f2f28;
	box-shadow: 10px 10px 0  -5px #af1c1c;
	transition: .3s;
	overflow: hidden;
	text-transform: uppercase;
	letter-spacing: 2px;
  outline: 2px solid #f0effe;
	cursor: pointer;
  margin: 0 15px;
}
.btn:before {
	display: inline-block;
	transition: bottom, 0.35s;
	position: absolute;
	transform: skew(-50deg);
	top: 0px;
	left: 260px;
	background-color: #af1c1c;
	content: "ã€€";
	width: 300px;
	height: 120px;
  z-index: -1;
}  
.btn:hover {
	box-shadow: 0px 0px 0  0px #af1c1c;
}
.btn:hover:before {
	top: -40px;
	left: -40px;
}
.btn:disabled {
  background-color: #af1c1c;
  box-shadow: none;
}
.alt{
  background-color: #af1c1c;
  box-shadow: 10px 10px 0  0px #31a2ac;
  outline: 5px solid #2f2f28;
  border: 5px solid#f0effe;
}
.alt::before{
  background-color: #31a2ac;
}
.nav-right{
  margin-right: 5vw;
}
.nav-search{
  width: 25vw;
  min-height: 40px;
  border: 3px solid #2f2f28;
  font-size: 22px;
}
/* Sidebar */
 .app-sidebar {
  width: 30%;
  height: 100vh;
  border-right: 1px solid #ddd;
  background: rgba(0, 0, 0, 0.02);
}
.app-sidebar-notes {
  height: calc(100vh - 78px);
  overflow-y: scroll;
}
.app-sidebar-note {
  padding: 25px;
  cursor: pointer;
}
.sidebar-note-title {
  display: flex;
  justify-content: space-between;
}
.app-sidebar-note button {
  color: crimson;
  font-size: 18px;
}
.app-sidebar-note p {
  margin: 10px 0;
}
.app-sidebar-note small {
  display: block;
  color: #999;
}
.app-sidebar-note:hover {
  background: #f0effe;
}
.app-sidebar-note.active,
.app-sidebar-note.active small {
  background: #999;
  color: #f0effe;
} 
/* Main */
.app-main {
  width: 70%;
  height: 100vh;
}
.app-main-note-edit,
.app-main-note-preview {
  height: 100vh;
} 
.no-active-note {
  width: 70%;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-size: 2rem;
  color: #999;
}
.app-main-last-modified{
  margin: 5px 20px;
  text-align: center;
  color: #999;
  font-size: 18px;
}
/* Editing */
.app-main-note-edit {
  padding: 25px;
}
.app-main-note-edit input,
textarea {
  display: block;
  border: 1px solid #ddd;
  margin-bottom: 20px;
  width: 100%;
   height: 70%; 
  padding: 10px;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}
.app-main-note-edit input {
  height: 50px;
  font-size: 2rem;
}
/* Preview */
.app-main-note-preview {
  border-top: 1px solid #ddd;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.02);
}
.preview-title {
  padding: 25px 25px 0 25px;
  margin: 0;
}
.markdown-preview {
  padding: 0 25px 25px 25px;
  font-size: 1rem;
  line-height: 2rem;
}
/* Modal */
.open-modal-btn {
  background: #f0effe;
  padding: 18px 25px;
  border-radius: 20px;
  font-weight: 500;
  border: 0;
  outline: none;
  font-size: 22px;
  cursor: pointer;
  line-height: 23px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.1s ease-in-out;
}
.open-modal-btn:hover {
  box-shadow: 0 8px 10px rgba(0, 0, 0, 0.1);
  transform: translateY(-1px);
}
.open-modal-btn:active {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  transform: translateY(0);
}
.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.5s ease-in-out;
}
.modal {
  background-color: #f0effe;
  width: 70%;
  max-width: 500px;
  max-height: 300px;
  padding: 40px 30px;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease-in-out;
  text-align: center;
}
.modalText{
  font-size: 20px;
}
.modal img {
  border-radius: 5px;
  width: 100%;
}
.modal svg {
  position: absolute;
  right: 10px;
  top: 10px;
  height: 24px;
  width: 24px;
  opacity: 0.3;
  cursor: pointer;
}
.modal svg:hover {
  opacity: 1;
}
.overlay.animated {
  opacity: 0;
  visibility: hidden;
}
.overlay.animated .modal {
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -30%);
  transition-delay: 0.2s;
}
.overlay.animated.show {
  opacity: 1;
  visibility: visible;
}
.overlay.animated.show .modal {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%);
}
@media (max-width: 550px) {
  nav .btn {
    margin: 0 5px;
    min-width: 50px;
  }
}